import React from "react";

import logosize from './images/logosize.svg';
import Footer from "./Footer";


import {
  BoxHeader,
  ContainerHeader,
  RowHeader,
  ColumnHeader,
  HeadingHeader,
  ImgagingHeader,
  StartButtonHeader
} from "./HeaderStyles";

function Navigation(props) {
  return (
      <BoxHeader>
        <div><ImgagingHeader src={logosize} alt="logo" /></div>
        <ContainerHeader>
        <RowHeader>
          <ColumnHeader>
            <HeadingHeader href="/">Home</HeadingHeader>
          </ColumnHeader>
          <ColumnHeader>
            <HeadingHeader href="/About">About Us</HeadingHeader>
          </ColumnHeader>
          <ColumnHeader>
            <HeadingHeader href="/Service">Services</HeadingHeader>
          </ColumnHeader>
          <ColumnHeader>
            <HeadingHeader href="/Contact">Contact Us</HeadingHeader>
          </ColumnHeader>
          <ColumnHeader><StartButtonHeader href="/LoginScreen">LET's GET STARTED</StartButtonHeader></ColumnHeader>
        </RowHeader>
      </ContainerHeader>
      <Footer />
    </BoxHeader>
   );
}

export default Navigation;