import React from "react";
import logo from './images/test.webp';
import Footer from "./Footer";

import {
    Box,
    Container,
    Row,
    Column,
    Column1,
    FooterLink,
    Heading,
    Imgaging,
    StartButton,
    InputBox,
  } from "./HomeStyles";

function Home() {
  return (
        <Box>
         <Container>
          <Row>
            <Column>
              <Heading>====</Heading>
              <Heading>Find your next perfect trip</Heading>
              <InputBox type="text" />
              <InputBox type="text" />
              <StartButton>Search</StartButton>
            </Column>
            <Column1>
              <Imgaging src={logo}></Imgaging>
            </Column1>
          </Row>
        </Container>
        <Footer />
      </Box>
  );
}

export default Home;