import React, { useState }  from 'react';
import { GoogleLogin, GoogleLogout } from 'react-google-login';
import FacebookLogin from 'react-facebook-login';

import Footer from "./Footer";

import {
    Box,
    Container,
    Row,
    Column1
 } from "./HomeStyles";


function LoginScreen() {

	const clientId = '731334390054-5b1vjkbg3c2me3qjipcig3jau24dcdlh.apps.googleusercontent.com'
	
	const success = response => {
  		console.log(response) // eslint-disable-line
	}

	const error = response => {
  		console.error(response) // eslint-disable-line
	}

	const logout = () => {
  		console.log('logout') // eslint-disable-line
	}



  return (
	<Box>
         <Container>
          <Row>
            <Column1></Column1>
            <Column1>
              	<GoogleLogin onSuccess={success} onFailure={error} clientId={clientId} />
    		<br />
    		<br />
    		<GoogleLogin theme="dark" onSuccess={success} onFailure={error} clientId={clientId} />
    		<br />
    		<br />
    		<GoogleLogin theme="dark" style={{ background: 'blue' }} onSuccess={success} onFailure={error} clientId={clientId} />
    		<br />
    		<br />
    		<GoogleLogout buttonText="Logout" onLogoutSuccess={logout} />
                <br />
                <br />
                <FacebookLogin appId="386211959824193" autoLoad={true} fields="name,email,picture" scope="public_profile,user_friends" icon="fa-facebook" />
            </Column1>
          </Row>
        </Container>
        <Footer />
      </Box>
  );
}

export default LoginScreen;